<template>
  <div class="book-list-comn">
      <div class="books">
        <ul>
          <li class="book" v-for="bookListData in bookListDatas" :key="bookListData.id">
            <div class="avater">
              <img :src="'http://statics.zhuishushenqi.com'+bookListData.cover" alt="">
            </div>
            <div class="txt">
              <h2 class="title">{{bookListData.title}}</h2>
              <p class="shortIntro">{{bookListData.shortIntro}}</p>            
              <span class="author">
                 <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-renwu"></use>
                </svg>
                {{bookListData.author}}
              </span>
            </div>
          </li>
        </ul>
      </div>
  </div>
</template>
<script>
export default{
    name:'bookList',
    props:{
      bookListDatas:Array
    },
    data(){
      return{    
      }
    }
}
</script>
<style lang="scss" scoped>
.book{
  display: flex;
  padding: 10px 20px 15px 15px;
  border-bottom: 1px solid #f0f1f2;
  .avater{
    width:66px; height: 100px;
    margin-right: 15px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .txt{
    flex: 1;
    .title{
      font-size: 16px;color: #333;
      margin-bottom: 10px;
    }
    .shortIntro{
      font-size: 14px;color: #666;
      line-height: 25px;
      overflow: hidden;text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp:2;
      margin-bottom: 5px;
    }
    .author{
      color: #666;
      .icon{
        font-size: 22px;
      }
    }
  }
}
</style>



