<template>
  <div class="catlist">
    <backbar :title="title"></backbar>
    <cartbar :gender="gender" :major='major' @selectType="onSelectType"></cartbar>
    <booklist :bookListDatas="bookListDatas"></booklist>
  </div>
</template>
<script>
import axios from 'axios'
import backbar from 'components/Backbar'
import cartbar from 'components/Cartbar'
import booklist from 'components/BookList'
export default {
  name:'catlist',
  data(){
    return{
      bookListDatas:[],
      major:'',
      title:'',
      gender:'',
      type:'hot',
      minor:'',
      start:0,
      limit:20
    }
  },
  created(){
  this.title=this.major=this.$route.params.major
  this.gender=this.$route.params.gender
  this.getCatBooks(this.$route.params.major)
  },
  methods:{
    getCatBooks(gender,type='hot',major='',minor='',start=0,limit=20){
      axios.get('/api/book/by-categories?', {
        params: {
          gender: this.gender,
          type: this.type,
          major: this.major,
          minor: this.minor,
          start: this.start,
          limit: this.limit
        }
      })
      .then((res)=>{     
        this.bookListDatas=res.data.books;
         console.log(this.bookListDatas)
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    onSelectType(data){
      console.log(data)
    }
  },
  components:{
    backbar,
    cartbar,
    booklist
  }
}
</script>
<style lang="scss" scoped>
 
</style>



