import Vue from 'vue'
import Router from 'vue-router'
import Category from 'pages/category'
import Featured from 'pages/featured'
import Rank from 'pages/rank'
import Shelf from 'pages/shelf'
import Catlist from 'pages/catlist'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'shelf',
      component: Shelf
    },
    {
      path: '/featured',
      name: 'featured',
      component: Featured
    },
    {
      path: '/category',
      name: 'category',
      component: Category
    },
    {
      path: '/rank',
      name: 'rank',
      component: Rank
    },
    {
      path: '/catlist',
      name: 'catlist',
      component: Catlist
    }
  ]
})



