<template>
  <div class="mheader"> 
      <div class="header-title">{{title}}</div>   
      <div class="search">
          <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-sousuo"></use>
            </svg>
      </div>
  </div>
</template>

<script>
export default {
  name:'mheader',
  props:{
      title:String
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped="">
    .mheader{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height:50px ;
        line-height: 50px;
        background: #ed424b;
        color: #ffffff;
        .search{
            width: 30px;
            height: 30px;
            color: #fff;
            position: absolute;
            padding: 10px;
            top: 0px;
            right: 0px;
            cursor: pointer;
        }
            .icon{
                 width: 30px;
                height: 30px;
                fill:currentColor;
            }
    }
    .header-title{
        margin-left: 20px;
        font-weight: bold;
        font-size: 20px;
    }
</style>




