<template>
  <div class="category">
    <mheader :title="hearderTitle"></mheader>
    <cat  v-for="category in categories" :category="category" :key="category.title"></cat>
    <tabbar></tabbar>
  </div>
</template>
<<script>
import axios from 'axios'
import tabbar from "components/Tabbar";
import cat from "components/Cat";
import mheader from "components/MHeader";
export default {
  name:'category',
  data(){
    return{
      categories:[],
      hearderTitle:'分类'
    }
  },
  created(){
    this.getCategoriesData()
  },
  methods:{
    getCategoriesData(){
     axios.get('/api/cats/lv2/statistics',{}).then((res)=>{
      for(const[key,value]of Object.entries(res.data)){
       let obj=null;
       switch (key) {
         case 'male':
           obj={
             title:'男生',
             gender:'male',
             catList:value
           }
           break;
        case 'female':
           obj={
             title:'女生',
             gender:'male',
             catList:value
           }
           break;
        case 'press':
           obj={
             title:'出版',
             gender:'press',
             catList:value
           }
           break;
         default:
           break;
       }
       if(obj!==null){
         this.categories.push(obj);
       }
      }
     })
    }
  },
  components:{
    mheader,
    cat,
    tabbar
  }
}
</script>
<<style lang="scss" scoped>
  .category{
    margin-top:50px;
    margin-bottom:80px;
  }
</style>



