<template>
  <div class="cat">
      <h3 class="cat-title">{{category.title}}</h3>
      <ul class="cat-list">
          <li v-for="cat in category.catList" :key="cat.name" class="cat-item">
             <router-link :to="{name:'catlist',params:{major:cat.name,gender:category.gender}}">
                 <p class="cat-name">{{cat.name}}</p>
                 <p class="cat-count">{{cat.bookCount}}</p>
             </router-link>
          </li>
      </ul>
  </div>
</template>
<<script>
export default {
  name:'cat',
  props:{
      category:Object
  },
  data(){
      return{};
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    .cat-title{
       height:75px;
      font-size: 20px;
       color:#333;
       line-height: 75px;
       border-bottom: 1px solid #cccccc;
       padding: 0 15px;
    }
    .cat-list{
        height: auto;
        overflow: hidden;    
    }
    .cat-item{
        width: 33%;
        float: left;
        text-align: center;
        height: 50px;
        padding: 15px 0;
        border-right: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        cursor: pointer;
        a{
            display: block;
        }
        .cat-name{
            font-size: 20px;
            margin-bottom: 10px
        }
        .cat-count{
            font-size: 15px;
            color: #999;
        }
    }
</style>





