<template>
  <div class="cartbar">
    <div class="cat-filter">
      <ul class="cat-filter-list">
        <li v-for="list in typeList" :key="list.value"  class="cat-filter-item">{{list.name}}</li>
      </ul>
    </div>
    <div class="cat-filter">
      <ul class="cat-filter-list">
          <li v-for="minor in minorList" :key="minor" class="cat-filter-item" @click="select($event,type)">
            {{minor}}
          </li>
      </ul>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name:'cartbar',
  props:{
    gender:String,
    major: String
  },
  data(){   
    return{
      minorList: ['全部'],
      typeList:[
        {value:'hot',name:'热门'},
        {value:'new',name:'新书'},
        {value:'reputation',name:'好评'},
        {value:'over',name:'完结'},
        {value:'monthly',name:'包月'}
      ]
    }
  },
  created(){
    this.getMinorList()
  },
  methods:{  
      getMinorList(){
        axios.get('/api/cats/lv2')
        .then(
           (res)=>{      
            for (let [index, elem] of res.data[this.gender].entries()) {
                if (elem.major === this.major) {
                  this.minorList.push(...elem.mins);
                  return;
                }
              }            
          })
      },
      select(event,type){      
        this.$emit('selectType','东方玄幻')
      }
    }
  }
  
</script>
<style lang="scss" scoped>
 .cartbar{
     margin-top: 50px;
     .cat-filter{
       height: 50px;
       border-bottom: 1px solid #dddddd;
       .cat-filter-item{
         float: left;line-height: 50px;
         font-size: 14px;padding: 0 10px;
         cursor: pointer;
       }
       &.active{
         color: #ed424b;
       }
     }
 }

</style>



