<template>
  <div class="backbar">
    <h2 class="title">{{title}}</h2>
    <div class="back-icon" @click="$router.back(-1)">
          <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-fanhui-copy"></use>
          </svg>
    </div>
  </div>
</template>
<script>
export default {
  name:'backbar',
  data () {
    return {
        title: null
    }
  },
  created () {
      this.title=this.major=this.$route.params.major
  }
}
</script>
<style lang="scss" scoped>
 .backbar{
   position: fixed;
   top: 0;
   height: 50px;
   line-height: 50px;text-align: center;
   background: #ed424b;
   color: #fff;
   left: 0;
   right: 0;
   .title{
     font-size: 24px;
   }
   .back-icon{
     position: absolute;
     width: 30px;
     height: 30px;
     margin: 10px;
     top: 0px;
     cursor: pointer;
   }
    .icon{
      font-size: 35px;
    }
 }
</style>



