<template>
  <div class="empty"> 
      <div class="empty-icon">
          <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-shujia1"></use>
            </svg>  
         <p class="empty-lable">快去添加你喜欢的小说吧</p>        
      </div>      
  </div>
</template>

<script>
export default {
  name:'empty'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped="">
   .empty{
       position: absolute;
       top: 50px;
       bottom: 80px;
       left: 0;
       right: 0;
       padding-top: 130px;
       text-align: center;
        .empty-icon{
            cursor: pointer;
        }
            .icon{
                width: 50px;
                height: 50px;
                margin-bottom:10px; 
            }
        .empty-lable{
            font-size: 15px;
            color: #666;
            text-align: center;
        }
   }
</style>




